import { ModuleWithProviders, NgModule } from '@angular/core';
import { gettext, hookNavigator, hookRoute, NavigatorNode, ViewContext } from '@c8y/ngx-components';
import {
  DatapointLibraryOptions,
  DATAPOINT_LIBRARY_CONFIG,
  pathToDatapointLibrary
} from '@c8y/ngx-components/datapoint-library/model';

@NgModule()
export class DatapointLibraryModule {
  static forRoot(
    config: DatapointLibraryOptions = {}
  ): ModuleWithProviders<DatapointLibraryModule> {
    return {
      ngModule: DatapointLibraryModule,
      providers: [
        hookRoute([
          {
            path: pathToDatapointLibrary,
            loadComponent: () =>
              import('@c8y/ngx-components/datapoint-library/list').then(
                m => m.DatapointLibraryListComponent
              )
          },
          {
            context: ViewContext.DatapointLibrary,
            path: 'details',
            loadComponent: () =>
              import('@c8y/ngx-components/datapoint-library/details').then(
                m => m.DatapointLibraryDetailsComponent
              ),
            label: gettext('Details'),
            icon: 'palette',
            priority: 500
          }
        ]),
        hookNavigator([
          new NavigatorNode({
            parent: gettext('Configuration'),
            label: gettext('Data point library'),
            featureId: 'dataPointLibrary',
            path: pathToDatapointLibrary,
            icon: 'c8y-data-points',
            routerLinkExact: false
          })
        ]),
        {
          provide: DATAPOINT_LIBRARY_CONFIG,
          useValue: config
        }
      ]
    };
  }
}
